<template>
  <div class="page-category-update">
    <div class="row">
      <div class="col-6 text-left">
        <h4></h4>
      </div>
      <div class="col-6 text-right">
        <a href="javascript:history.go(-1)" class="btn btn-outline-primary"></a>
      </div>
      <div class="col-12">
        <hr>
      </div>
    </div>
    <form @submit.prevent="videoUpdateSubmit()" class="form">
    <div class="form-group row">
      <label for class="col-form-label col-2"></label>
      <div class="col-10">
        <input
          type="text"
          class="form-control"
          placeholder=""
          v-model="videoData.title"
        >
      </div>
    </div>
    <div class="form-group row">
      <label for class="col-form-label col-2"></label>
      <div class="col-10">
        <input
          type="text"
          class="form-control"
          placeholder=""
          v-model="videoData.description"
        >
      </div>
    </div>
    <div class="form-group row">
      <label for class="col-form-label col-2"></label>
      <div class="col-10">
        <input
          type="text"
          class="form-control"
          placeholder=""
          v-model="videoData.url"
        >
      </div>
    </div>
    <div class="form-group row">
      <label for class="col-form-label col-2"></label>
      <div class="col-10">
        <input
          type="text"
          class="form-control"
          placeholder=""
          v-model="videoData.cover"
        >
        <img :src="videoData.cover" height="200"/>
      </div>
    </div>
    <div class="form-group row">
      <label for class="col-form-label col-2"></label>
      <div class="col-10">
        <input
          type="text"
          class="form-control"
          placeholder=""
          v-model="videoData.sort"
        >
      </div>
    </div>
    <div class="form-group row">
      <div class="col-12 text-right">
        <hr>
        <input type="hidden" v-model="videoData.id">
        <input type="submit" value="" class="btn btn-success pl-5 pr-5">
      </div>
    </div>
  </form>
  </div>
</template>
<script>
import Request from "./../../api/common/request";
export default {
  data() {
    return {};
  },
  asyncData({ store, route }) {
    let videoId = route.query.id || 0;
    if(videoId){
      Request.get("/api/video/detail", { video_id: videoId }).then(ret => {
        console.log("request video detail", ret);
        store.state.videoData = ret.data;
      });
    }else{
      alert('')
       this.$router.go(-1);
    }
  },
  computed: {
    videoData() {
      return this.$store.state.videoData;
    }
  },
  methods: {
      async videoUpdateSubmit(){
        let videoData = this.videoData;
        console.log("videoUpdateSubmit().videoData", videoData);
        let ret = await Request.post("/api/video/update", videoData);
        if (ret.code === 0) {
          // location.href = '/'
          alert("");
          this.$router.go(-1);
        } else {
          alert(":" + ret.message);
        }
      },

  },

}
</script>